import { Button, Fab, FormControl, InputLabel, makeStyles, MenuItem, Paper, Select, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Theme, Tooltip } from '@material-ui/core'
import React, {useEffect, useState} from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { changeRole, deleteUser, getUsers } from '../../redux/reducers/usersReducer'
import AddIcon from '@material-ui/icons/Add';


const useStyles = makeStyles((theme:Theme) => ({
    table: {
      minWidth: 650,
    },
    formControl: {
        margin: theme.spacing(1),
        minWidth: 120,
    },
    selectEmpty: {
        marginTop: theme.spacing(2),
    },
}));

export const AdminPanel = () => {
    const classes = useStyles()

    const [editMode, setEditMode] = useState(false)
    const dispatch = useDispatch()
    const selectUsers = useSelector((state:any) => state.users.users)

    useEffect(() => {
        dispatch(getUsers())
    }, [])

    const onDeleteHandle = (id: string) => {
        dispatch(deleteUser(id))
    }

    return (
        <div>
            <TableContainer component={Paper}>
                <Table className={classes.table} aria-label="simple table">
                    <TableHead>
                        <TableRow>
                            <TableCell>Login</TableCell>
                            <TableCell align="right">Role</TableCell>
                            <TableCell align="right">Name</TableCell>
                            <TableCell align="right">Last name</TableCell>
                            <TableCell align="right">Actions</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {selectUsers.map((user:any) => {
                            return <TableRow key={user.id}>
                                    <TableCell component="th" scope="row">
                                        {user.login}
                                    </TableCell>
                                    <TableCell align="right">{user.role}</TableCell>
                                    <TableCell align="right">{user.name}</TableCell>
                                    <TableCell align="right">{user.lastName}</TableCell>
                                    <TableCell align="right">
                                        {
                                            editMode 
                                            ? <> 
                                                <EditForm setEditMode={setEditMode} id={user.id}/>
                                            </>
                                            : <> 
                                                <Button color="secondary" onClick={() => onDeleteHandle(`${user.id}`)}> Delete </Button>
                                                <Button color="primary" onClick={() => setEditMode(true)}> Edit </Button>
                                            </>
                                        }
                                    </TableCell>
                                </TableRow>
                        })}
                    </TableBody>
                </Table>
            </TableContainer>
            <Tooltip title="Add" aria-label="add">
            <Fab color="primary" >
                <AddIcon />
            </Fab>
            </Tooltip>
        </div>
    )
}



const EditForm = (props:any) => {
    const classes = useStyles()
    const {setEditMode, id} = props

    const dispatch = useDispatch()

    const onRoleHandler = (target:any) => {
        dispatch(changeRole(`${id}`, target))
        setEditMode(false)
    }

    return (
        <FormControl className={classes.formControl}>
            <InputLabel id="demo-simple-select-label">Role</InputLabel>
            <Select
                labelId="demo-simple-select-label"
                id="demo-simple-select"
                onChange={(event) => onRoleHandler(event.target.value)}
            >
            <MenuItem value={'editor'}>Editor</MenuItem>
            <MenuItem value={'author'}>Author</MenuItem>
            <MenuItem value={'reviewer'}>Reviewer</MenuItem>
            <MenuItem value={'admin'}>Admin</MenuItem>
            </Select>
        </FormControl>
    )
}


